.content-wrapper {

	@media #{$breakpoint-tablet} {
		margin: 0 auto;
		max-width: 80%;
	}

	p,
	ul {
		font-weight: 300;
		font-size: 1.2rem;
		line-height: 2;
	}

	h1,h2 {
		font-size: 2em;
		margin: 35px 0 0 0;
	}

	a {
		color: $color-blue;
		text-decoration: none;

		&:hover {
			color: $color-orange;
			text-decoration: underline;
		}
	}
}


.breadcrumb {
	border-bottom: $border-primary;
	border-top: $border-primary;
	display: flex;
	justify-content: space-between;
	list-style: none;
	margin: 45px 0;
	padding: 10px 0;

	@media #{$breakpoint-mobile} {
		flex-wrap: wrap;
	}
}

.breadcrumb-item {
	display: inline;

	a {
		align-items: center;
		color: $color-dark;
		display: flex;
		text-decoration: none;

		&:hover {
			color: $color-orange;
		}
	}
}

.breadcrumb-name {
	line-height: 0;
	padding: 0 5px;
}

.breadcrumb-item--last {
	@media #{$breakpoint-mobile} {
		margin-left: auto;
	}
}

blockquote {
	border-left: 4px solid $color-grey;
	font-style: italic;
	margin: 0;
	padding-left: 20px;
}



